import React, { useState } from 'react';

type Props = {
  open: boolean;
  onClose: () => void;
  onSubmit: (deviceId: string, pin: string) => void;
  deviceId: string | null;
  recipientMsisdn?: string;
  helperMessage?: string;
};

export default function PinModal({ open, onClose, onSubmit, deviceId, recipientMsisdn, helperMessage }: Props) {
  const [pin, setPin] = useState('');
  if (!open) return null;

  return (
    <div style={backdrop}>
      <div style={modal}>
        <h3>   </h3>
        <p style={confirmText}>
              
          {recipientMsisdn ? (
            <>
              : <span style={{ direction: 'ltr', unicodeBidi: 'plaintext', marginInlineStart: 6, color: '#dc2626', fontWeight: 700 }}>{recipientMsisdn}</span>
            </>
          ) : null}
        </p>
        {helperMessage ? (<p style={hintText}>{helperMessage}</p>) : null}
        
        <input
          type="password"
          value={pin}
          onChange={(e) => setPin(e.target.value)}
          onKeyDown={(e) => {
            if (e.key === 'Enter') {
              if (deviceId) {
                onSubmit(deviceId, pin);
                setPin('');
              }
            }
          }}
          placeholder=""
          style={input}
          autoFocus
        />
        <div style={{ display: 'flex', justifyContent: 'flex-end', gap: 10 }}>
          <button onClick={onClose} style={cancelBtn}></button>
          <button
            onClick={() => {
              if (deviceId) {
                onSubmit(deviceId, pin);
                setPin('');
              }
            }}
            style={okBtn}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
}

const backdrop: React.CSSProperties = {
  position: 'fixed',
  inset: 0 as any,
  background: 'rgba(0,0,0,0.4)',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  zIndex: 1000,
};

const modal: React.CSSProperties = {
  background: '#fff',
  padding: 20,
  borderRadius: 10,
  width: 400,
  boxShadow: '0 10px 30px rgba(0,0,0,0.3)',
};

const input: React.CSSProperties = {
  width: '100%',
  padding: 10,
  borderRadius: 8,
  border: '1px solid #ddd',
  marginBottom: 15,
  fontSize: 16,
  textAlign: 'center',
};

const okBtn: React.CSSProperties = { background: '#22c55e', color: '#fff', border: 'none', padding: '8px 14px', borderRadius: 6 };
const cancelBtn: React.CSSProperties = { background: '#f3f4f6', border: 'none', padding: '8px 14px', borderRadius: 6 };

const hintText: React.CSSProperties = {
  fontSize: 12,
  color: '#6b7280',
  marginTop: 4,
  marginBottom: 8,
  textAlign: 'right',
};

const confirmText: React.CSSProperties = {
  fontSize: 13,
  color: '#111827',
  marginBottom: 6,
  textAlign: 'right',
  fontWeight: 600,
};
