/**
 *         / .
 *    localStorage        .
 */

const KEY_BASE = 'wallet_selection_pin_required';

function getKey(userId?: string) {
  return userId ? `${KEY_BASE}_${userId}` : KEY_BASE;
}

export const walletSelectionPinService = {
  isEnabled(userId?: string): boolean {
    try {
      const val = localStorage.getItem(getKey(userId));
      return val === 'true';
    } catch {
      return false;
    }
  },
  setEnabled(enabled: boolean, userId?: string): void {
    try {
      localStorage.setItem(getKey(userId), enabled ? 'true' : 'false');
    } catch {}
  }
};