import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { useToast } from '@/hooks/use-toast';
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext';
import { db, firebaseConfig } from '@/firebase';
import { collection, addDoc, query, where, onSnapshot, orderBy, serverTimestamp, deleteDoc, doc, setDoc, getFirestore, updateDoc } from 'firebase/firestore';
import { Users, Plus, Loader2, Trash2, CheckCircle, Clock, XCircle, AlertTriangle, Ban, DollarSign } from 'lucide-react';
import { initializeApp, deleteApp } from 'firebase/app';
import { getAuth, createUserWithEmailAndPassword, signOut } from 'firebase/auth';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { SubscriptionPlan } from '@/utils/subscriptionService';

interface ResellerAccount {
  id: string;
  uid?: string;
  email: string;
  password?: string;
  phone?: string;
  status: 'pending' | 'active' | 'inactive';
  packageName?: string;
  ownerId: string;
  createdAt: any;
  startDate?: any;
  endDate?: any;
  deletionRequested?: boolean;
  deletionReason?: string;
  deletionStatus?: 'pending' | 'rejected';
  cancelRequested?: boolean;
  cancelReason?: string;
  cancelStatus?: 'pending' | 'rejected';
}

const PLANS = [
  { value: SubscriptionPlan.MONTHLY, label: ' ' },
  { value: SubscriptionPlan.QUARTERLY, label: '  ' },
  { value: SubscriptionPlan.YEARLY, label: ' ' },
  { value: SubscriptionPlan.LIFETIME, label: '  ' },
];

export const ResellerManagement = () => {
  const { user } = useFirebaseAuth();
  const { toast } = useToast();
  const [accounts, setAccounts] = useState<ResellerAccount[]>([]);
  const [loading, setLoading] = useState(true);
  const [isResellerEnabled, setIsResellerEnabled] = useState(false);
  const [isDialogOpen, setIsDialogOpen] = useState(false);
  const [isPricingDialogOpen, setIsPricingDialogOpen] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [formData, setFormData] = useState({ email: '', password: '', phone: '', packageName: '' });

  // Delete Request States
  const [isDeleteRequestOpen, setIsDeleteRequestOpen] = useState(false);
  const [deleteReason, setDeleteReason] = useState('');
  const [selectedAccountForDelete, setSelectedAccountForDelete] = useState<string | null>(null);
  const [isDeleteSubmitting, setIsDeleteSubmitting] = useState(false);

  // Cancel Package Request States
  const [isCancelRequestOpen, setIsCancelRequestOpen] = useState(false);
  const [cancelReason, setCancelReason] = useState('');
  const [selectedAccountForCancel, setSelectedAccountForCancel] = useState<string | null>(null);
  const [isCancelSubmitting, setIsCancelSubmitting] = useState(false);

  useEffect(() => {
    if (!user) return;

    // 1. Check if reseller system is enabled for this user
    const userDocRef = doc(db, 'users', user.uid);
    const unsubscribeUser = onSnapshot(userDocRef, (docSnap) => {
      if (docSnap.exists()) {
        const data = docSnap.data();
        setIsResellerEnabled(data.isResellerEnabled === true);
      }
    });

    // 2. Fetch reseller accounts
    const q = query(
      collection(db, 'reseller_requests'),
      where('ownerId', '==', user.uid)
    );

    const unsubscribeAccounts = onSnapshot(q, (snapshot) => {
      const docs = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() } as ResellerAccount));
      //         
      docs.sort((a, b) => {
        const timeA = a.createdAt?.seconds || 0;
        const timeB = b.createdAt?.seconds || 0;
        return timeB - timeA;
      });
      setAccounts(docs);
      setLoading(false);
    }, (error) => {
      console.error("Error fetching reseller accounts:", error);
      setLoading(false);
    });

    return () => {
      unsubscribeUser();
      unsubscribeAccounts();
    };
  }, [user]);

  const generateReferralCode = (userId: string) => {
    const timestamp = Date.now().toString(36);
    const userHash = userId.slice(-6);
    return `REF_${userHash}_${timestamp}`.toUpperCase();
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!user) {
      toast({ title: '', description: '   ', variant: 'destructive' });
      return;
    }
    if (!formData.email || !formData.password || !formData.phone || !formData.packageName) {
      toast({ title: '', description: '   ', variant: 'destructive' });
      return;
    }

    setIsSubmitting(true);
    let secondaryApp: any = null;

    try {
      // 1.    Firebase Auth   
      //          
      secondaryApp = initializeApp(firebaseConfig, "SecondaryApp");
      const secondaryAuth = getAuth(secondaryApp);
      const secondaryDb = getFirestore(secondaryApp);
      
      const userCredential = await createUserWithEmailAndPassword(secondaryAuth, formData.email, formData.password);
      const newUser = userCredential.user;
      const userReferralCode = generateReferralCode(newUser.uid);
      
      // 2.         (  )
      //  secondaryDb      
      
      // .   
      await setDoc(doc(secondaryDb, 'users', newUser.uid), {
        email: formData.email,
        displayName: ' ',
        phone: formData.phone,
        shopName: null,
        isActive: false, //      
        role: 'merchant',
        referralCode: userReferralCode,
        ownerId: user.uid, //    ()  
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
        lastLogin: null
      });

      // .    
      const shopRef = await addDoc(collection(secondaryDb, 'shops'), {
        name: ' ',
        ownerId: newUser.uid,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
        isActive: true
      });

      // .    (Profile)
      await setDoc(doc(secondaryDb, 'profiles', newUser.uid), {
        userId: newUser.uid,
        shopId: shopRef.id,
        role: 'merchant',
        fullName: ' ',
        phone: formData.phone,
        referralCode: userReferralCode,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp()
      });

      //     
      await signOut(secondaryAuth);

      // 3.     (   )
      //  db  
      await addDoc(collection(db, 'reseller_requests'), {
        ownerId: user.uid,
        uid: newUser.uid, //     
        email: formData.email,
        password: formData.password, //        
        phone: formData.phone,
        packageName: formData.packageName,
        shopId: shopRef.id,
        status: 'pending',
        createdAt: serverTimestamp(),
      });
      
      toast({ title: ' ', description: '      ' });
      setIsDialogOpen(false);
      setFormData({ email: '', password: '', phone: '', packageName: '' });
    } catch (error: any) {
      console.error("Error adding account:", error);
      let errorMessage = '    ';
      if (error.code === 'auth/email-already-in-use') {
        errorMessage = '   ';
      } else if (error.code === 'auth/weak-password') {
        errorMessage = '   ';
      } else if (error.code === 'auth/invalid-email') {
        errorMessage = '   ';
      }
      toast({ title: '', description: errorMessage, variant: 'destructive' });
    } finally {
      if (secondaryApp) {
        try {
          await deleteApp(secondaryApp);
        } catch (e) {
          console.error("Error deleting secondary app:", e);
        }
      }
      setIsSubmitting(false);
    }
  };

  const handleCancelClick = (account: ResellerAccount) => {
    setSelectedAccountForCancel(account.id);
    setCancelReason('');
    setIsCancelRequestOpen(true);
  };

  const handleSubmitCancelRequest = async () => {
    if (!selectedAccountForCancel) return;
    if (!cancelReason.trim()) {
      toast({ title: '', description: '   ', variant: 'destructive' });
      return;
    }

    setIsCancelSubmitting(true);
    try {
      await updateDoc(doc(db, 'reseller_requests', selectedAccountForCancel), {
        cancelRequested: true,
        cancelReason: cancelReason,
        cancelStatus: 'pending'
      });
      toast({ title: ' ', description: '      ' });
      setIsCancelRequestOpen(false);
    } catch (error) {
      console.error("Error submitting cancel request:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    } finally {
      setIsCancelSubmitting(false);
    }
  };

  const handleDeleteClick = (account: ResellerAccount) => {
    // If pending, allow direct delete (cancel request)
    if (account.status === 'pending') {
      if (window.confirm('       ')) {
        handleDelete(account.id, account.uid);
      }
      return;
    }

    // Otherwise open delete request dialog
    setSelectedAccountForDelete(account.id);
    setDeleteReason('');
    setIsDeleteRequestOpen(true);
  };

  const handleSubmitDeleteRequest = async () => {
    if (!selectedAccountForDelete) return;
    if (!deleteReason.trim()) {
      toast({ title: '', description: '   ', variant: 'destructive' });
      return;
    }

    setIsDeleteSubmitting(true);
    try {
      await updateDoc(doc(db, 'reseller_requests', selectedAccountForDelete), {
        deletionRequested: true,
        deletionReason: deleteReason,
        deletionStatus: 'pending'
      });
      toast({ title: ' ', description: '     ' });
      setIsDeleteRequestOpen(false);
    } catch (error) {
      console.error("Error submitting delete request:", error);
      toast({ title: '', description: '    ', variant: 'destructive' });
    } finally {
      setIsDeleteSubmitting(false);
    }
  };

  const handleDelete = async (id: string, uid?: string) => {
    try {
      // 1.   
      await deleteDoc(doc(db, 'reseller_requests', id));
      
      // 2.      users   uid
      if (uid) {
        try {
          await deleteDoc(doc(db, 'users', uid));
        } catch (userError) {
          console.error("Error deleting user doc:", userError);
          //       
        }
      }

      toast({ title: ' ', description: '   ' });
    } catch (error) {
      console.error("Error deleting request:", error);
      toast({ title: '', description: '   ', variant: 'destructive' });
    }
  };

  const getStatusBadge = (status: string, packageName?: string) => {
    switch (status) {
      case 'active':
        return (
          <div className="flex items-center gap-1 text-green-600 bg-green-50 px-2 py-1 rounded-full text-xs">
            <CheckCircle className="w-3 h-3" />
            <span> {packageName ? `- ${packageName}` : ''}</span>
          </div>
        );
      case 'pending':
        return (
          <div className="flex items-center gap-1 text-yellow-600 bg-yellow-50 px-2 py-1 rounded-full text-xs">
            <Clock className="w-3 h-3" />
            <span> </span>
          </div>
        );
      case 'inactive':
        return (
          <div className="flex items-center gap-1 text-red-600 bg-red-50 px-2 py-1 rounded-full text-xs">
            <XCircle className="w-3 h-3" />
            <span> </span>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <Card className={`shadow-lg mb-6 bg-white dark:bg-slate-900 border border-gray-200 dark:border-slate-800 relative overflow-hidden ${!isResellerEnabled ? 'min-h-[500px]' : ''}`}>
      {!isResellerEnabled && (
        <div className="absolute inset-0 z-50 backdrop-blur-sm bg-yellow-50/50 flex flex-col items-center justify-center p-6 text-center">
          <div className="bg-white p-6 rounded-full shadow-lg mb-4">
            <Ban className="w-12 h-12 text-red-500" />
          </div>
          <h3 className="text-xl font-bold text-gray-900 mb-2">   </h3>
          <p className="text-gray-600 max-w-md mb-6">
                 .            .
          </p>
          <Button 
            onClick={() => setIsPricingDialogOpen(true)}
            className="bg-green-600 hover:bg-green-700 text-white gap-2"
          >
            <DollarSign className="w-4 h-4" />
             
          </Button>


        </div>
      )}
      <CardHeader className="flex flex-row items-center justify-between">
        <CardTitle className="flex items-center gap-2 dark:text-white">
          <Users className="h-5 w-5" />
           
        </CardTitle>
        <div className="flex items-center gap-2">
          <Button 
            size="sm"
            onClick={() => setIsPricingDialogOpen(true)}
            className="bg-green-600 hover:bg-green-700 text-white gap-2 shadow-sm"
          >
            <DollarSign className="w-4 h-4" />
             
          </Button>
          <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
            <DialogTrigger asChild>
              <Button size="sm" className="gap-2 bg-blue-600 hover:bg-blue-700 text-white shadow-sm">
                <Plus className="h-4 w-4" />
                 
              </Button>
            </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>   </DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4 mt-4">
              <div className="space-y-2">
                <Label> </Label>
                <Input 
                  type="email" 
                  value={formData.email}
                  onChange={(e) => setFormData(prev => ({ ...prev, email: e.target.value }))}
                  placeholder="email@example.com"
                  required
                />
              </div>
              <div className="space-y-2">
                <Label> </Label>
                <Input 
                  type="tel" 
                  value={formData.phone}
                  onChange={(e) => setFormData(prev => ({ ...prev, phone: e.target.value }))}
                  placeholder="01xxxxxxxxx"
                  required
                />
              </div>
              <div className="space-y-2">
                <Label> </Label>
                <Select
                  value={formData.packageName}
                  onValueChange={(value) => setFormData(prev => ({ ...prev, packageName: value }))}
                  required
                >
                  <SelectTrigger className="text-right" dir="rtl">
                    <SelectValue placeholder=" " />
                  </SelectTrigger>
                  <SelectContent>
                    {PLANS.map((plan) => (
                      <SelectItem key={plan.value} value={plan.value} className="text-right" dir="rtl">
                        {plan.label}
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <Label> </Label>
                <Input 
                  type="text" 
                  value={formData.password}
                  onChange={(e) => setFormData(prev => ({ ...prev, password: e.target.value }))}
                  placeholder="  "
                  required
                />
              </div>
              <Button type="submit" className="w-full bg-blue-600 hover:bg-blue-700 text-white" disabled={isSubmitting}>
                {isSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                 
              </Button>
            </form>
          </DialogContent>
        </Dialog>
        </div>
      </CardHeader>
      <CardContent>
        {loading ? (
          <div className="text-center py-4 text-gray-500"> ...</div>
        ) : accounts.length === 0 ? (
          <div className="text-center py-8 text-gray-500 bg-gray-50 rounded-lg border border-dashed border-gray-200">
                
          </div>
        ) : (
          <div className="space-y-3">
            {accounts.map((account) => (
              <div key={account.id} className="flex items-center justify-between p-3 rounded-lg border border-gray-100 bg-gray-50/50 hover:bg-gray-50 transition-colors">
                <div className="space-y-1">
                  <div className="font-medium text-sm text-gray-900 dark:text-gray-100">{account.email}</div>
                  <div className="text-xs text-gray-400 flex flex-col gap-0.5">
                    {account.status === 'active' && account.startDate && account.endDate ? (
                      <>
                        <span className="text-green-600">: {new Date(account.startDate.seconds * 1000).toLocaleDateString('en-GB')}</span>
                        <span className="text-red-500">: {new Date(account.endDate.seconds * 1000).toLocaleDateString('en-GB')}</span>
                      </>
                    ) : (
                      <span>{account.createdAt?.seconds ? new Date(account.createdAt.seconds * 1000).toLocaleDateString('en-GB') : ''}</span>
                    )}
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  {getStatusBadge(account.status, account.packageName)}
                  
                  {/* Badge for deletion rejected status */}
                  {account.deletionStatus === 'rejected' && (
                    <div className="flex items-center gap-1 text-red-600 bg-red-50 px-2 py-1 rounded-full text-xs border border-red-100">
                      <XCircle className="w-3 h-3" />
                      <span>   </span>
                    </div>
                  )}

                  {/* Badge for deletion pending status */}
                  {account.deletionRequested && account.deletionStatus === 'pending' && (
                    <div className="flex items-center gap-1 text-orange-600 bg-orange-50 px-2 py-1 rounded-full text-xs border border-orange-100">
                      <Clock className="w-3 h-3" />
                      <span> </span>
                    </div>
                  )}

                  {/* Badge for cancellation pending status */}
                  {account.cancelRequested && account.cancelStatus === 'pending' && (
                    <div className="flex items-center gap-1 text-orange-600 bg-orange-50 px-2 py-1 rounded-full text-xs border border-orange-100">
                      <Clock className="w-3 h-3" />
                      <span>  </span>
                    </div>
                  )}

                  {/* Badge for cancellation rejected status */}
                  {account.cancelStatus === 'rejected' && (
                    <div className="flex items-center gap-1 text-red-600 bg-red-50 px-2 py-1 rounded-full text-xs border border-red-100">
                      <XCircle className="w-3 h-3" />
                      <span>   </span>
                    </div>
                  )}

                  {account.status === 'active' && (
                    <Button 
                      variant="ghost" 
                      size="icon" 
                      className="text-orange-500 hover:text-orange-700 hover:bg-orange-50 h-8 w-8"
                      onClick={() => handleCancelClick(account)}
                      disabled={account.cancelRequested && account.cancelStatus === 'pending'}
                      title=" "
                    >
                      <Ban className="h-4 w-4" />
                    </Button>
                  )}

                  <Button 
                    variant="ghost" 
                    size="icon" 
                    className="text-red-500 hover:text-red-700 hover:bg-red-50 h-8 w-8"
                    onClick={() => handleDeleteClick(account)}
                    disabled={account.deletionRequested && account.deletionStatus === 'pending'}
                  >
                    <Trash2 className="h-4 w-4" />
                  </Button>
                </div>
              </div>
            ))}
          </div>
        )}
      </CardContent>

      {/* Delete Request Dialog */}
      <Dialog open={isDeleteRequestOpen} onOpenChange={setIsDeleteRequestOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2 text-red-600">
              <AlertTriangle className="h-5 w-5" />
                
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <p className="text-sm text-gray-600">
                     .      .
            </p>
            <div className="space-y-2">
              <Label> </Label>
              <Textarea 
                value={deleteReason}
                onChange={(e) => setDeleteReason(e.target.value)}
                placeholder="  ..."
                rows={4}
              />
            </div>
            <Button 
              onClick={handleSubmitDeleteRequest} 
              className="w-full bg-red-600 hover:bg-red-700 text-white"
              disabled={isDeleteSubmitting}
            >
              {isDeleteSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      {/* Cancel Package Request Dialog */}
      <Dialog open={isCancelRequestOpen} onOpenChange={setIsCancelRequestOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle className="flex items-center gap-2 text-orange-600">
              <Ban className="h-5 w-5" />
                
            </DialogTitle>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <p className="text-sm text-gray-600">
                      .      .
              <br />
              <span className="text-xs text-red-500 font-semibold">:       .</span>
            </p>
            <div className="space-y-2">
              <Label> </Label>
              <Textarea 
                value={cancelReason}
                onChange={(e) => setCancelReason(e.target.value)}
                placeholder="  ..."
                rows={4}
              />
            </div>
            <Button 
              onClick={handleSubmitCancelRequest} 
              className="w-full bg-orange-600 hover:bg-orange-700 text-white"
              disabled={isCancelSubmitting}
            >
              {isCancelSubmitting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : null}
                
            </Button>
          </div>
        </DialogContent>
      </Dialog>
      <Dialog open={isPricingDialogOpen} onOpenChange={setIsPricingDialogOpen}>
        <DialogContent className="max-w-2xl">
          <DialogHeader>
            <DialogTitle className="text-center text-xl font-bold mb-4">  </DialogTitle>
          </DialogHeader>
          <div className="overflow-x-auto">
            <table className="w-full text-right border-collapse">
              <thead>
                <tr className="bg-gray-100">
                  <th className="p-3 border text-gray-700"> </th>
                  <th className="p-3 border text-blue-600">  </th>
                  <th className="p-3 border text-red-600"> </th>
                  <th className="p-3 border text-green-600"> </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td className="p-3 border font-medium"> </td>
                  <td className="p-3 border">150 .</td>
                  <td className="p-3 border">100 .</td>
                  <td className="p-3 border font-bold text-green-600">50 .</td>
                </tr>
                <tr>
                  <td className="p-3 border font-medium">  </td>
                  <td className="p-3 border">350 .</td>
                  <td className="p-3 border">250 .</td>
                  <td className="p-3 border font-bold text-green-600">100 .</td>
                </tr>
                <tr>
                  <td className="p-3 border font-medium"> </td>
                  <td className="p-3 border">1150 .</td>
                  <td className="p-3 border">900 .</td>
                  <td className="p-3 border font-bold text-green-600">250 .</td>
                </tr>
                <tr>
                  <td className="p-3 border font-medium">  </td>
                  <td className="p-3 border">3000 .</td>
                  <td className="p-3 border">2400 .</td>
                  <td className="p-3 border font-bold text-green-600">600 .</td>
                </tr>
              </tbody>
            </table>
          </div>
        </DialogContent>
      </Dialog>
    </Card>
  );
};
